import nusantaraSource from '../../data/nusantara-source';
import { createMasakanItemTemplate } from '../templates/masakan-template';
import searchEngine from '../../utils/search-initiator';
import ICON from '../../component/icon';

const MasakanList = {
    // TODO: membuat tampilan home
    async render() {
        return `
            <hero-section></hero-section>
            <explore-section></explore-section>
            <div id="spinner" hidden></div>
            <div class="offline-info" id="offline" hidden>
                <img src="${ICON._offline}" alt="internet offline">
                <span>Ups</span>
                <p>Silahkan cek koneksi internet kamu dan coba lagi!</p>
            </div>
            <reviewers-section></reviewers-section>
            <partner-section></partner-section>
        `;
    },

    async afterRender() {
        // TODO: mendapatkan data restaurant
        const restaurant = await nusantaraSource.restaurantItem();
        const restaurantContainer = document.querySelector('restaurant-list');

        // TODO: menampilkan data restaurant
        const spinner = document.getElementById('spinner');
        const offlineState = document.getElementById('offline');

        try {
            if (restaurant) {
                spinner.removeAttribute('hidden');
                setTimeout(() => {
                    spinner.setAttribute('hidden', '');
                    restaurant.forEach((data) => {
                        restaurantContainer.innerHTML += createMasakanItemTemplate(data);
                    });

                    // TODO: menampilkan data berdasarkan pencarian
                    const temp = document.querySelector('restaurant-list');
                    temp.insertAdjacentHTML('afterend', `
                    <div class="search-info-not-found" style="display:none;" id="info-active">
                        <div style="display: flex; align-items: center; row-gap: 1em; flex-direction: column;">
                            <img src="${ICON._empty_culnary}" alt="dodongkal" style="max-width: 400px; height: 100%; width: 100%; border-radius: 30px;">
                            <p style="font-size: x-large">Maaf, kami tidak dapat menemukan data yang anda cari!</p>
                        </div>
                    </div>
                    `);

                    const searchBar = document.querySelector('#search-data');
                    const restaurantItem = document.querySelectorAll('.restaurant-data');
                    searchEngine(searchBar, restaurantItem);
                }, 1000);
            } else {
                offlineState.removeAttribute('hidden', '');
            }
        } catch (error) {
            console.log(error);
        }

        // TODO: menampilkan halaman selalu di atas
        window.scrollTo(0, 0);
    },
};

export default MasakanList;
