import UrlParser from '../../routes/url-parser';
import nusantaraSource from '../../data/nusantara-source';
import { createDetailMasakanTemplate, createOurReviewerTemplate } from '../templates/masakan-template';
import FavoriteButtonPresenter from '../../utils/favorite-button-presenter';
import FavoriteRestaurantIdb from '../../data/favorite-restaurant-idb';
import { generateId, dateId } from '../../utils/auto-generate';
import ICON from '../../component/icon';

const Detail = {
    async render() {
        return `
        <div id="show-detail">
            <div id="spinner" hidden></div>
            <div class="offline-info" id="offline" hidden>
                <img src="${ICON._offline}" alt="internet offline">
                <span>Ups</span>
                <p>Silahkan cek koneksi internet kamu dan coba lagi!</p>
            </div>
        </div>
        `;
    },

    async afterRender() {
        // TODO: mendapatkan detail restaurant berdasarkan id
        const url = UrlParser.parseActiveUrlWithoutCombiner();
        const restaurants = await nusantaraSource.detailRestaurant(url.id);
        const result = restaurants.filter((data) => data.masakanId === url.id);

        // TODO: menampilkan data restaurant
        const restaurantContainer = document.querySelector('#show-detail');

        // TODO: spinner dan offline state
        const spinner = document.getElementById('spinner');
        const offlineState = document.getElementById('offline');

        if (result) {
            spinner.removeAttribute('hidden');
            setTimeout(() => {
                spinner.setAttribute('hidden', '');
                restaurantContainer.innerHTML = createDetailMasakanTemplate(result);

                // TODO: menampilkan tombol favorite
                FavoriteButtonPresenter.init({
                    favoriteButtonContainer: document.querySelector('#favoriteButtonContainer'),
                    favoriteRestaurants: FavoriteRestaurantIdb,
                    restaurant: {
                        masakanId: result[0].masakanId,
                        namaMasakan: result[0].namaMasakan,
                        imageUrl: result[0].imageUrl,
                        deskripsi: result[0].deskripsi,
                        provinsi: result[0].provinsi,
                        tanggal: result[0].tanggal,
                        rating: result[0].rating,
                        reviewerMasakan: result[0].reviewerMasakan,
                    },
                });

                // TODO: membuat rating
                const stars = document.querySelectorAll('.stars i');
                stars.forEach((star, index1) => {
                    star.addEventListener('click', () => {
                        // eslint-disable-next-line no-shadow
                        stars.forEach((star, index2) => {
                            // eslint-disable-next-line no-unused-expressions
                            index1 >= index2 ? star.classList.add('active') : star.classList.remove('active');
                        });
                        countRating();
                    });
                });

                // TODO: menambahkan reviews
                const formReview = document.querySelector('#myForm');
                const reviewContainer = document.querySelector('#review-users');

                formReview.addEventListener('submit', async (event) => {
                    event.preventDefault();
                    const inputName = document.getElementById('input-name');
                    const inputImage = document.getElementById('input-image');
                    const inputAge = document.getElementById('input-age');
                    const inputWork = document.getElementById('input-work');
                    const inputComment = document.getElementById('input-comment');
                    const fotoUser = ICON._users;

                    const dataForm = {
                        reviewerId: generateId(),
                        namaReviewer: inputName.value,
                        imageUrl: inputImage.value ? inputImage.value : fotoUser,
                        umur: inputAge.value,
                        pekerjaan: inputWork.value,
                        rating: `${countRating() <= 0 ? '1' : countRating()}`,
                        tanggal: dateId(),
                        komentar: inputComment.value,
                    };

                    result[0].reviewerMasakan.push(dataForm);
                    reviewContainer.innerHTML = `
                        ${result[0].reviewerMasakan.map((read) => createOurReviewerTemplate(read)).join(' ')}
                    `;
                });

                // TODO: Mengambil nilai rating
                const countRating = () => {
                    const rateId = document.querySelectorAll('.stars .active');
                    return rateId.length;
                };
            }, 1000);
        } else {
            offlineState.removeAttribute('hidden', '');
        }

        // TODO: menampilkan halaman selalu di atas
        window.scrollTo(0, 0);
    },
};

export default Detail;
