import ICON from '../../component/icon';
import FavoriteRestaurantIdb from '../../data/favorite-restaurant-idb';
import { createMasakanItemTemplate } from '../templates/masakan-template';

const Favorite = {
    // TODO: membuat tampilan utama favorite page
    async render() {
        return `
            <restaurant-favorite id="restaurants"></restaurant-favorite>
        `;
    },

    // TODO: mengambil data dari IndexedDB (FavoriteMoviedb)
    async afterRender() {
        const restaurants = await FavoriteRestaurantIdb.getAllRestaurants();
        const restaurantContainer = document.querySelector('#restaurants');

        if (restaurants.length >= 1) {
            restaurants.forEach((data) => {
                restaurantContainer.innerHTML += createMasakanItemTemplate(data);
            });
        } else {
            restaurantContainer.insertAdjacentHTML('afterend', `
            <div class="favorite-not-found" style="display: flex; align-items: center; row-gap: 1em; flex-direction: column;">
                <img src="${ICON._empty}" alt="sate" style="max-width: 300px; height: 100%; width: 100%;">
                <div style="font-size: x-large;">Tidak ada kuliner yang disimpan di sini!</div>
            </div>
            `);
        }

        // TODO: menampilkan halaman selalu di atas
        window.scrollTo(0, 0);
    },
};

export default Favorite;
